body {
  background-color: #282c34;
  color: white;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.address-link {
    color: #35d07f;
}

.loading-icon-container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  .background {
    opacity: 0.5;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #0e0e0e;
    z-index: 100;
  }
  .loading-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 101;
  }
}



.table {
  min-width: 650px;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.modal {
  position: absolute;
  width: 400px;
  left: 30%;
  top: 30%;
  border: 2px solid #000;
  padding: 10px;
  background-color: white;
}

.card {
  margin: 10px;
}
